* {
    padding: 0;
    margin: 0;
  }
 
  body {
    font-family: sans-serif;
  }
 
  a,
  a:visited {
    outline: none;
  }
 
  a:hover {
    text-decoration: none;
    cursor: pointer;
  }
 
  .header {
    text-align: center;
  }

  button {
    margin-top: 20px;
    line-height: 60px;
    font-weight: bold;
    padding: 0 40px;
    background: salmon;
    border: none;
  }
  button:hover {
    background: lightsalmon;
  }

  .nav {
    width: 500px;
    margin: 20px auto;
    max-width: 48em;
    border: 1px solid #333;
    border-left: none;
    border-right: none;
    text-align: center;
  }
 
  .menu {
background-color:grey;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  }
 
  .menu:hover {
    background: #ddd;
  }
 
  .menu.is-active {
    background: #ddd;
  }

  .Merah.is-active{
    background: red;
  }

 .Kuning.is-active{
    background: yellow;
  }
 
  .Hijau.is-active{
    background: green;
  }
 
  .Biru.is-active{
    background: aqua;
  }
 
  .Merah.selected{
    background: red;
    width: 100%;
    height: 100%;
  }

 .Kuning.selected{
    background: yellow;
  }
 
  .Hijau.selected{
    background: green;
  }
 
  .Biru.selected{
    background: aqua;
  }

  .info {
    margin-top: 40px;
    text-align: center;
  }
 
  .selected {
    margin-left: 5px;
    padding: 5px 10px;
    vertical-align: middle;
    border-radius: 4px;
    background: #ddd;
    text-transform: capitalize;
    font-weight: bold;
    font-size: 1.5em;
  }